<template>
  <nav :class="current == 'HomePage' ? wushe : youshe" id="mainNav">
    <div class="container px-4 px-lg-5">
      <a class="navbar-brand" @click="huan(assembly[0])">首页</a>
      <button
        class="navbar-toggler navbar-toggler-right"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarResponsive"
        aria-controls="navbarResponsive"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarResponsive">
        <ul class="navbar-nav ms-auto my-2 my-lg-0">
          <li class="nav-item" v-for="(item, key) in name" :key="key">
            <a
              class="nav-link"
              :style="current == assembly[key + 1] ? yanshe : ''"
              @click="huan(assembly[key + 1])"
              >{{ item }}</a
            >
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import data from "../data/data.json";
export default {
  props: {
    current: String,
  },
  name: "Navigation",
  data() {
    return {
      yanshe: {
        color: "#f4623a",
      },
      wushe: "navbar navbar-expand-lg navbar-light fixed-top py-3",
      youshe:
        "navbar navbar-expand-lg navbar-light fixed-top py-3 navbar-shrink",
      assembly: data.page.assembly,
      name: data.page.name,
    };
  },
  methods: {
    huan(i) {
      this.$emit("SwitchPages", i);
    },
  },
};
</script>

<style scoped>
.nav-item,
.navbar-brand {
  cursor: pointer;
}
</style>
