<template>
  <div id="LeavingAmessage">
    <div class="DisplayPanel">
      <div class="LeavingAmessage" v-for="(item, key) in data" :key="key">
        <div class="text">{{ item[1] }}</div>
        <div class="name">
          <div>{{ item[0] }}</div>
          <div>{{ item[2] }}</div>
        </div>
      </div>
    </div>
    <div class="input">
      <input
        class="name"
        placeholder="昵称"
        type="text"
        v-model="name"
        @keyup.enter="texts"
        autofocus
      />
      <input
        class="text"
        placeholder="留言"
        type="text"
        v-model="input"
        ref="text"
        @keyup.enter="get"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["data1", "data2"],
  name: "LeavingAmessage",
  data() {
    return {
      name: "",
      input: "",
      data: this.data2,
      text: "",
    };
  },
  methods: {
    texts() {
      this.$refs.text.focus();
    },
    get() {
      if (this.name == "" || this.input == "") {
        alert("昵称或留言没有填写，这样是不行的哦~");
      } else {
        let input = this.input;
        let time = new Date().toLocaleDateString();
        this.input = "";
        const land = AV.Object.extend("LeavingAmessage");
        const data = new land();
        data.set("title", [this.name, input, time]);
        data.save().then(
          (i) => {
            this.data.push([this.name, input, time]);
            this.$emit("data2", this.data);
          },
          (i) => {
            alert("网络出错啦！");
          }
        );
      }
    },
  },
};
</script>

<style scoped>
.LeavingAmessage {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
  justify-content: space-between;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 3px;
  color: rgba(0, 0, 0, 0.6);
}
.LeavingAmessage .name {
  font-size: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.LeavingAmessage .text {
  padding-right: 20px;
}
.input {
  width: 100%;
}

.input > * {
  padding-left: 10px;
  border: 1px solid gray;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
  outline: none;
}

.input .name {
  width: 30%;
}
.input .text {
  width: 70%;
}
</style>