<template>
  <div class="page-section" id="notebook">
    <div id="packing">
      <input
        id="text"
        placeholder="回车添加事件"
        type="text"
        v-model="event"
        @keyup.enter="add"
      />
      <div id="hangInTheAir">
        <div>未完成：</div>
        <ul class="Store">
          <li class="one" v-for="(item, key) in Store" :key="key">
            <span class="label">{{ key + 1 }}</span>
            <span class="content">{{ item }}</span>
            <span class="delete" @click="success(key)">完成呐</span>
          </li>
        </ul>

        <div class="BottomBar" v-show="Store.length > 0">
          <div class="label">还有{{ Store.length }}件事要做哦</div>
          <div class="delete" @click="successAll">全都完成呐!</div>
        </div>
      </div>
      <div id="Completed">
        <div>已完成：</div>
        <ul class="Store">
          <li class="one" v-for="(item, key) in Completed" :key="key">
            <span class="label">{{ key + 1 }}</span>
            <span class="content">{{ item }}</span>
            <span class="delete" @click="strikeOut(key)">清除</span>
          </li>
        </ul>
        <div class="BottomBar" v-show="Completed.length > 0">
          <div class="label">已经完成{{ Completed.length }}件事呐！</div>
          <div class="delete" @click="clearAll">全部清除</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "notebook",
  data() {
    return {
      event: "",
      Store: localStorage.local == "" ? [] : localStorage.local.split(" "),
      Completed:
        localStorage.Completed == "" ? [] : localStorage.Completed.split(" "),
    };
  },
  methods: {
    successAll() {
      this.Completed = this.Store;
      localStorage.Completed = localStorage.local;
      this.Store = [];
      localStorage.local = "";
    },
    clearAll() {
      this.Completed = [];
      localStorage.Completed = "";
    },
    add() {
      if (this.event) {
        this.Store.push(this.event);
        localStorage.local = this.Store.join(" ");
        this.event = "";
      } else {
        alert("什么都没有写哦");
      }
    },
    success(i) {
      let a = this.Store.splice(i, 1);
      this.Completed.push(a[0]);
      localStorage.local = this.Store.join(" ");
      localStorage.Completed = this.Completed.join(" ");
    },
    strikeOut(i) {
      this.Completed.splice(i, 1);
      localStorage.Completed = this.Completed.join(" ");
    },
  },
  beforeCreate() {
    if (!localStorage.local && !localStorage.Completed) {
      localStorage.local = "";
      localStorage.Completed = "";
    }
  },
};
</script>

<style scoped>
#notebook {
  min-height: calc(100vh - 53px);
  display: flex;
  justify-content: center;
}
#packing {
  padding: 20px;
  border: 1px solid gray;
  border-radius: 10px;
  background: url("../../public/assets/img/background-4.jpg");
  background-position: top;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
}
@media only screen and (max-width: 360px) {
  #packing {
    width: 100%;
  }
}
#text {
  padding: 5px 10px;
  width: 100%;
  border: 1px solid gray;
  outline-color: lightskyblue;
  border-radius: 3px;
  background-color: transparent;
}
#hangInTheAir {
  margin-top: 20px;
}
#Completed {
  margin-top: 50px;
}
.Store {
  padding: 0;
}
.one {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2px 10px;

  margin-top: 5px;
  border-radius: 5px;
}
#hangInTheAir .one {
  color: red;
  border: 1px solid red;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.80) 0%, rgba(255, 255, 255, 0.80) 100%);
}
#Completed .one {
  color: green;
  border: 1px solid green;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.80) 0%, rgba(255, 255, 255, 0.80) 100%);
}
.label {
  font-size: 12px;
  line-height: 24px;
}
.delete {
  font-size: 12px;
  line-height: 24px;
  cursor: pointer;
}
.BottomBar {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-top: 2px solid black;
}
</style>