<template>
  <div id="portfolio">
    <div class="container-fluid p-0">
      <div class="row g-0">
        <div v-for="(item, key) in works" :key="key" class="col-lg-4 col-sm-6">
          <a class="portfolio-box" :title="item[1]" @click="huan(key)">
            <img
              class="img-fluid"
              :src="'assets/img/portfolio/' + key + '.' + item[2]"
              alt="..."
            />
            <div class="portfolio-box-caption">
              <div class="project-category text-white-50">{{ item[0] }}</div>
              <div class="project-name">{{ item[1] }}</div>
            </div>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import works from "../data/data.json"
export default {
  name: "Portfolio",
  data() {
    return {
        works:works.works,
        zhujian: works.app,
    };
  },
  methods: {
    huan(i) {
      this.$emit("chuan", this.zhujian[i]);
    },
  },
};
</script>

<style scoped>
#portfolio {
  padding-top: 72px;
  min-height: 100vh;
}
</style>